import React, { useState, useEffect } from 'react';
import withBackground from '../../hoc/withBackground/withBackground';
import CardRows from '../../components/UI/CardRows/CardRows';
import CardCarousel from '../../components/UI/CardCarousel/CardCarousel';
import styles from './Work.module.css';

import weightSrc from '../../assets/images/Work/WeightTracker.png';
import medSrc from '../../assets/images/Work/MedicationReminder.png';
import subSrc from '../../assets/images/Work/SubManagement.png';
import appleSrc from '../../assets/images/Work/AppleTV.png';
import nikeSrc from '../../assets/images/Work/Nike.png';
import dailySrc from '../../assets/images/Work/TheDaily.png';

const cardsData = [
  {
    src: weightSrc,
    alt: "Weight Tracker",
    to: "/weight-tracker",
    leftText: "Hims & Hers",
    rightText: "Weight Tracker",
  }, {
    src: medSrc,
    alt: "Medication Reminder",
    to: "/medication-reminder",
    leftText: "Hims & Hers",
    rightText: "Medication Reminders",
  }, {
    src: subSrc,
    alt: "Sub Management",
    to: "/subscription-management",
    leftText: "Hims & Hers",
    rightText: "Sub Management",
  }, {
    src: appleSrc,
    alt: "Apple TV+ Press",
    to: "/apple",
    leftText: "Apple",
    rightText: "Apple TV+ Press",
  }, {
    src: nikeSrc,
    alt: "Nike Fleece",
    to: "/nike",
    leftText: "Nike",
    rightText: "Nike Fleece",
  }, {
    src: dailySrc,
    alt: "The Daily by NYT",
    to: "/the-daily",
    leftText: "Gretel",
    rightText: "The Daily by NYT",
  }
];

const breakpoint = 600;

const Work = () => {
  const [isCarousel, setIsCarousel] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsCarousel(window.innerWidth >= breakpoint);
    };
    window.addEventListener('resize', handleResize);
    handleResize();

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <div className={styles["work"]}>
      {isCarousel ? <CardCarousel cardsData={cardsData} /> : <CardRows cardsData={cardsData} />}
    </div>
  )
}

export default withBackground(Work);